import React, { PureComponent } from 'react'
import { BrowserRouter, Route, Link, Redirect } from "react-router-dom";
import axios from "axios";
import {API_URL} from "../../config/config";




class UserControl extends PureComponent {
    static propTypes = {}

    constructor(props) {
        super(props)

        this.state = {
            user_list : []
        }
    }
    componentDidMount(){
        axios.post(API_URL+'/car_booking/production/user/getAll.php').then((data)=>{
            this.setState({user_list:data.data})
            console.log('data',data.data)
        })    
    }
    render() {
        const user = this.state.user_list.map((row,index) =>{
             return <tr>
                    <td>
                        {index+1}
                    </td>
                    <td>
                        {row.first_name}
                    </td>
                    <td>
                        {row.department}
                    </td>
                    <td>
                        {row.username}
                    </td>                              
                    <td className="td-actions ">
                        <Link  to={"/user-edit/"+row.id}>
                            <button type="button" rel="tooltip" title="Edit Task"  className="btn btn-primary btn-link btn-sm">
                            <i className="material-icons">edit</i>
                            </button>
                        </Link>
                        <Link  to={"/user-del/"+row.id}>
                            <button type="button" rel="tooltip" title="Remove" className="btn btn-danger btn-link btn-sm">
                            <i className="material-icons">close</i>
                            </button>
                        </Link>    
                        
                    </td>
                    </tr>
        })
        return (
            <div className="content">
                            <div className="container-fluid">
                            <div className="row">
                                <div className="col-md-12">
                                <div className="card">
                                    <div className="card-header card-header-success">
                                        <div className="row">
                                            <div className="col-10">
                                                <h4 className="card-title ">User Management</h4>
                                                <p className="card-category">User list</p>
                                            </div>
                                            <div className="col-2">
                                                <Link  to="/user-add">
                                                    <button type="button" rel="tooltip" title="add" className="btn btn-primary card-img-right">
                                                        <i className="material-icons">note_add</i>
                                                    </button>
                                                </Link>
                                            </div>
                                        </div>
                                        
                                    </div>
                                    
                                    <div className="card-body">
                                    <div className="table-responsive">
                                        <table className="table">
                                        <thead className=" text-primary">
                                            <tr>
                                                <th>
                                                ID
                                                </th>
                                            
                                            
                                                <th>
                                                Name
                                                </th>
                                            
                                            
                                                <th>
                                                Department
                                                </th>
                                            
                                            
                                                <th>
                                                User
                                                </th>
                                            
                                        
                                                <th>
                                                Action
                                                </th>
                                            
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {user}
                                        </tbody>
                                        </table>
                                    </div>
                                    </div>
                                </div>
                                </div>
                            </div>
                            </div>
                </div>
        )
    }
}

export default UserControl